import React, { Component } from 'react'
import axios from 'axios'
import './App.css'

class App extends Component {
  constructor(props) {
    super(props)
    this.state = {
      formData: {
        text: '',
        title: '',
        author: ''
      }
    }
  }

  updateField(field, value) {
    const { formData } = this.props
    this.setState({
      formData: {
        ...formData,
        [field]: value
      },
      result: null
    })
  }

  check(e) {
    e.preventDefault()
    const bodyFormData = new FormData()
    for (let key in this.state.formData) {
      if (this.state[key]) {
        bodyFormData.append(key, this.state[key])
      }
    }
    this.setState({
      result: 'Fake'
    })
    axios
      .post('/running', bodyFormData)
      .then(res => {
        this.setState({
          result: 'Fake'
        })
      })
  }

  render() {
    const { result } = this.state
    return (
      <div className="App">
        <div className="app-inner-wrap">
          <h1>Fake or not?</h1>
          <p>Paste in the text of an article, and we'll run it through a predictive model to see whether it is reliable or not.</p>
          <p>Note: this is meant to be a fun, easy way to view the results of a predictive model, not an authoritative reliability checker. In fact, try to find topics/phrases that would trigger a "Fake news!" response even with a reliable input.</p>
          <form className="form" onSubmit={(e) => this.check(e)}>
            <label className="label">Text</label>
            <textarea
              type="text"
              rows="10"
              className="input-text"
              onChange={(e) => this.updateField('text', e.target.value)}
            />
            <label className="label">Title (optional)</label>
            <input
              type="text"
              className="input-title"
              onChange={(e) => this.updateField('title', e.target.value)}
            />
            <label className="label">Author (optional)</label>
            <input
              type="author"
              className="input-author"
              onChange={(e) => this.updateField('author', e.target.value)}
            />
            <button type="submit" onClick={(e) => this.check(e)}>Run now!</button>
          </form>
          {result && (<div className="result-wrap">
            <p className="result-label">Result:</p>
            <p className="result">{result}</p>
          </div>)}
        </div>
      </div>
    )
  }
}

export default App;
